	module.exports = {"title":"El año de Sofía","date":"2015-01-01","layout":"post","path":"/el-año-de-sofia/","category":"pensamiento","description":"Ayer desenfundé a Otto Scharmer. Para quien no lo conozca es un tipo que ha desarrollado parte de su carrera profesional en el MIT analizando los modelos de aprendizaje y los procesos de innovación. Me pica mucho su planteamiento, sobre todo por haberme declarado en el pasado como totalmente opuesto a los planteamientos de la fenomenología que Otto maneja con fluidez e inteligencia.","body":"<p>Ayer desenfundé a Otto Scharmer. Para quien no lo conozca es un tipo que ha desarrollado parte de su carrera profesional en el MIT analizando los modelos de aprendizaje y los procesos de innovación. Me pica mucho su planteamiento, sobre todo por haberme declarado en el pasado como totalmente opuesto a los planteamientos de la fenomenología que Otto maneja con fluidez e inteligencia.\nU-Theory ha significado para mí, junto a otras obras que han poblado 2014 como The Element de Ken Robinson, The Ignorant Schoolmaster de Ranciere, Dialogue de Isaacs, varias de Jon Kabat-Zinn, alguna incursión en la mecánica cuántica (Bohm, Brian Greene…) y un repaso a algunas obras de neurociencia y biología evolutiva, un gran acicate para recuperar mi gran pasión por el pensamiento y la investigación de la cosa común.</p>\n<p>Quizá el 2015, que muchos apuntan como el año de la revolución democrática lo sea realmente y las ágoras del siglo XXI de nuevo se pueblen de Logos Koinós. Lou Marinoff afirmaba que la receta para esta sociedad enferma de sí misma era “más Platón y menos Prozac”, yo estoy en condiciones de afirmar, hoy, el penúltimo día del año, que, en proporciones variables en función de cada caso, la combinación fluoxetina/sophia me ha servido para superar un año muy duro, que habría sido imposible sin muchos de los que me apoyáis y me amáis, pero sobre todo sin mi mujer, la mejor.\n<a href=\"http://www.ottoscharmer.com\">http://www.ottoscharmer.com</a></p>\n"}

