	module.exports = {"title":"Crea tu propia red de WordPress","date":"2015-02-05","layout":"post","path":"/crea-tu-propia-red-de-wordpress/","category":"desarrollo web","description":"Desde la versión 3.0 WordPress, el gestor de contenidos más popular en la web (alcanzando casi el 50% de todos los sitios web publicados)), nos permite configurar una red de blogs","body":"<p>Desde la versión 3.0 <a href=\"https://wordpress.org/\" title=\"Wordpress\">WordPress</a>, <strong>el gestor de contenidos más popular en la web</strong> (alcanzando casi el 50% de todos los sitios web publicados <a href=\"http://trends.builtwith.com/cms\" title=\"CMS Technologies Web Usage Statistics\">http://trends.builtwith.com/cms</a>), nos permite configurar una <strong>red de blogs</strong>, una funcionalidad anteriormente disponible en <a href=\"https://mu.wordpress.org/\" title=\"Multisite Wordrpess\">WordPress Multisite</a>. Esta funcionalidad tiene especial interés si queremos construir <strong>una comunidad de sitios web similar a <a href=\"http://WordPress.com\">WordPress.com</a></strong>, asignando a cada uno de los sitios una URL personalizada, pero compartiendo todos ellos un mismo dominio. Imagina una <strong>web corporativa</strong> en la que queremos incluir distintos blogs, de temáticas diferentes, o de autores diferentes, identificados en la <strong>URL con subdominios diferentes</strong> pero todos ellos compartiendo el mismo dominio, algo así como:</p>\n<pre><code>tecnología.dominio.com\nemprendimiento.dominio.com\nformacion.dominio.com`\n</code></pre>\n<p>Pues bien, para ello tendríamos que <strong>hacer que cada subdominio apuntase a un blog diferente</strong>, todos ellos relacionados y formando una única red que se podría administrar conjuntamente, especificando los plugins disponibles, los temas y otros elementos comunes.</p>\n<p>Si sabemos de antemano el número de blogs que vamos a crear tendremos que <strong>añadir a la configuración de Apache el nombre de los subdominios</strong>, como por ejemplo en:</p>\n<pre><code>ServerName dominio.com\nServerAlias dominio.com subdominio1.domain.com\nsubdominio2.dominio.com\nDocumentRoot\n/var/www/directorio_en_el_que_está_instalado_wordpress`\n</code></pre>\n<p>Pero quizá <strong>querramos ir creando distintos blogs</strong>, o bien <strong>permitir que los usuarios creen su propio blog sin tener que estar modificando la configuración de nuestro servidor web</strong>. Para ello incluiremos un <strong>wildcard</strong> en el nombre del servidor, quedando la configuración de la siguiente manera:</p>\n<pre><code>ServerName wordpress.dev\nServerAlias *.wordpress.dev\nServerAdmin webmaster@localhost\nDocumentRoot\n/var/www/html/directorio_en_el_que_está_instalado_wordpress`\n</code></pre>\n<p>Recuerda además de <strong>incluir las siguientes directivas</strong> en el directorio en el que quieres instalar WordPress:</p>\n<pre><code>&lt;Directory\n/var/www/html/directorio_en_el_que_está_instalado_wordpress&gt;\nAllowOverride Fileinfo Options\nAllowOverride All\n&lt;/Directory&gt;`\n</code></pre>\n<p>Ahora el siguiente paso será <strong>editar tu archivo wp-config.php</strong> para incluir lo siguiente:</p>\n<pre><code>/* Multisite */\ndefine( 'WP_ALLOW_MULTISITE', true );\ndefine('MULTISITE', true);\ndefine('SUBDOMAIN_INSTALL', true);\ndefine('DOMAIN_CURRENT_SITE', 'dominio.com');\ndefine('PATH_CURRENT_SITE', '/');\ndefine('SITE_ID_CURRENT_SITE', 1);\ndefine('BLOG_ID_CURRENT_SITE', 1);`\n</code></pre>\n<p>Eso es todo. Ya tenemos nuestra red de WordPress activa y cada uno de los distintos blogs asignado a un subdominio diferente.</p>\n"}

