	module.exports = {"title":"Je Suis Andreas","date":"2015-04-01","layout":"post","path":"/je-suis-andreas/","category":"actualidad","description":"Tras varios días de rumor informativo poco a poco se ha ido desvelando la causa de los hechos atroces que acabaron con la vida de más de un centenar de personas el pasado 25 de marzo en Los Alpes franceses.","body":"<p>Tras varios días de rumor informativo poco a poco se ha ido desvelando la causa de los hechos atroces que acabaron con la vida de más de un centenar de personas el pasado 25 de marzo en Los Alpes franceses. Los medios de información, las multinacionales del transporte de pasajeros y el conjunto de la sociedad occidental ha hablado: el copiloto del vuelo, un chaval de 27 años que quería volar amante del deporte y de la comida rápida decidió acabar con su vida y la de 150 personas más una soleada mañana de marzo, pocos días después del eclipse solar y el cambio de estación. Llevaba meses a tratamiento psiquiátrico y había abandonado su periodo de formación aquejado de una crisis depresiva. Ya está. El loco. Fue el loco.</p>\n<p>Con la locura hemos topado. En esta sociedad líquida, tal y como la califica el sociólogo polaco Zygmunt Bauman, en la que el individuo y la individuación se han convertido en el mito del triunfo, el loco se postula como el trasgresor, aquél que se atreve a dudar de los marcos, de los límites de lo socialmente aceptado. Y si esa locura es aún más peligrosa quizá le lleve a dudar de su propia vida o, ¡oh anatema!, de la de los otros…</p>\n<p>Hoy muchos somos Andreas (invocando el reciente “je suis Charlie”) pero no en el sentido que cabe esperar de la expresión solidaria: se trata de una identidad silenciosa, cabizbaja, que se reconoce solamente a los más íntimos o movido por la urgencia de explicar lo inexplicable. Muchos visitamos las consultas de salud mental aquejados por una pena, por un tic que ya se ha convertido en obsesión, por una relación que ha acabado antes de tiempo, o quizá mucho después… Todos somos locos. Pequeñas locuras que nos mueven a escribir un poema y dejarlo abandonado en un portal extraño, esperando que los dedos del amado lo devuelvan a la vida tras interpretar un mensaje críptico a altas horas de la madrugada, a amar en tiempos de guerra civil permanente, a emprender un proyecto con el único apoyo de los pies de otros locos como nosotras. Una locura pasajera, que durará como mucho tanto como dure la vida. Una locura inofensiva, cariñosa, amable, graciosa a veces.</p>\n<p>Pero no fue esa locura la que condujo a Andreas a apuntar el morro del Airbus a las montañas vigilantes que allá abajo parecían alzar los brazos pidiendo socorro, o tal vez solamente un poco de cariño. La locura que mató a Andreas, y con el a 149 personas más, viste zapatillas deportivas de infinitos colores, a veces pantalones pitillo, con el dobladillo a diez centímetros del tobillo, bebe Coca Cola, cena en Burguer King con los amigos, sueña con ser el mejor y follarse a la más guapa. La locura que mató a esas 150 personas y sus sueños viste trajes de mil euros y repasa el peinado frente al espejo cada mañana buscando el parecido con ese as del fútbol que acaba de tuitear su última conquista o una foto de su flamante deportivo. La locura que chocó el pasado 25 de marzo contra una colina de Los Alpes franceses es la que mira el cronómetro con ansiedad y repasa el lunes los resultados de la carrera del domingo buscando los apellidos de ese compañero de trabajo que nos mira por encima del hombro o de ese jefe al que no osamos decir de una maldita vez lo que pensamos del proyecto que nos atarea de 9 a 6 cinco días a la semana y once meses al año. La locura asesina ha posado en infinitas ocasiones con los labios abultados mostrando algún monumento o alguna estrella de la televisión junto a una bonita cara. La locura más peligrosa no tiene los ojos inyectados en sangre ni las mejillas rojas de tanto frotarse las lágrimas. Ella no se oculta en una gripe o en una mala digestión para no ser señalada. Ella tiene pista libre para desplegar sus alas sin ningún complejo.</p>\n<p>No podemos señalar al padre como responsable de la miseria de los hijos, ni aún mucho menos de la falta de amor de su mujer. Responsabilizar a la compañía aérea de su falta de celo en los controles médicos, o a los padres por ocultar al vecindario la silenciosa enfermedad que no impedía a Andreas ser el vecino simpático que ha conseguido alcanzar su sueño es mucho más sencillo que reconocer nuestra incapacidad de ser felices, nuestro sometimiento a la voluntad de poder, que de tanto matar dioses ha terminado por ocultar las nuevas divinidades en los escaparates de los centros comerciales.</p>\n<p>El pasado miércoles 25 de marzo, a las 10:40 de la mañana 150 seres humanos dejaron de soñar, renunciando a una vida repleta de inquietantes aventuras, de amores fugitivos, de nombres que se olvidan con los años pero que reviven con el inapreciable aroma de un mueble viejo o de una especia que apenas sabemos nombrar. Ese mismo día, a las 12:45 comenzaba en Telecinco un programa de televisión que muchos aspirantes a Superman esperaban con ansiedad, buscando quizá curar la zozobra de esa liquidez a la que apunta el señor Bauman, o tal vez el mero placer de unos bonitos pechos antes de dar rienda suelta a la concupiscencia del mamífero anabolizado asado y las verduras transgénicas aderezadas con salsas ricas en glutamato. Sin embargo la actualidad del morbo por descubrir entre los amasijos de metal un brazo o una mano, como el vestigio de la humanidad desaparecida en la montaña, impidió la emisión del fantástico magazine, lo cual provocó un aluvión de críticas en las redes sociales y alguna que otra denuncia por xenofobia y otras lindezas provocadas por la gomina a diario. Esa es la locura que empujó al joven Andreas a la barbarie. Pero mientras no hay pastillas ni diagnóstico que la iluminen seguiremos sufriéndola en silencio y tragando sus consecuencias con la más universal de las solidaridades. Es por eso que hoy más que nunca deberíamos juntarnos todos en la plaza, o en el ágora digital que más nos exponga y gritar a coro: “Je suis Andreas”</p>\n"}

