	module.exports = {"title":"Working Class Heroes","date":"2015-05-01","layout":"post","path":"/working-class-heroes/","category":"memorias","description":"Todas, cuando somos pequeñitas, compartimos ideas absurdas y surreales acerca de la realidad que nos rodea. Supongo que muchas podríais añadir a esta historia mil y una anécdotas que la empequeñecerían hasta hacerla tan insignificante como una puesta de sol.","body":"<p>Todas, cuando somos pequeñitas, compartimos ideas absurdas y surreales acerca de la realidad que nos rodea. Supongo que muchas podríais añadir a esta historia mil y una anécdotas que la empequeñecerían hasta hacerla tan insignificante como una puesta de sol. Para mí, aun pasados más de treinta y bastantes años, esta fantasía sigue siendo real en cierta medida, quizá porque la ensoñación supera con creces en interés emocional e incluso artístico a la realidad.</p>\n<p>He vivido muchas tardes de primavera tras el colegio en la casa de mis abuelos, tras un paseo por la ciudad pequeñita e inmensa de un niño de cuatro años, de la Plaza de Pontevedra a la Gaiteira, pasando por San Diego para recoger una poca de hierba para los conejos y por algún kiosko para arañar algunas pesetas en postalillas o paracaidistas de plástico a mi abuelo.</p>\n<p>Aquella casa, que más tarde supe que era una chabola-cueva tenía tres espacios. A la estancia principal, con puerta, que ejercía de salón, comedor, cocina y dormitorio del menor de mis tíos, se accedía desde una especie de porche improvisado que había convertido el patio frontal de la vivienda en un pequeño almacén y un cuarto de baño, en el que la ciscerna se accionaba con el grifo del lavabo y que olía a varondandy. Se trataba de un espacio de apenas 6 metros cuadrados con dos o tres televisiones, varios radiocassettes y chorizos y tocinos colgados del techo junto a los fogones. Dentro había dos habitaciones más, con una cortina a modo de puerta, escarbadas en la piedra, que sudaban pintura y siempre estaban fresquitas. En una de ellas dormían mis abuelos, con la cama atravesada de lado a lado con un minúsculo espacio al otro lado, absurdo, pues apenas cabían los pies, pero supongo que condicionado por la forma de la pieza. En la otra había dormido mi tío Jose. Era el cuarto del misterio. Había fotos, cintas de cassette y muchas cosas pequeñas que yo no podía tocar. Entraba a hurtadillas cuando mis abuelos se despistaban y, sin encender la luz, hurgaba entre las mil y una maravillas que me aguardaban en aquel cuarto misterioso junto a la nevera.</p>\n<p>Yo era muy pequeño, quizá tres o cuatro años, a lo sumo seis. En ese contexto surgió la idea absurda que me acompañó durante toda mi infancia y que tardé mucho en refutar pues me parecía mucho más atractiva que la realidad que la había inspirado. Mi tío era John Lennon.</p>\n<p>Yo estaba convencido. Allí lo tenía con sus gafitas redondas, su melena lacia, como los indios de las películas de vaqueros que veía con mi abuelo. Allí estaba. Lennon era noticia. Su asesinato era reciente, o su apogeo, o su maldición, pues no recuerdo exactamente el año en que surgió esta idea absurda en mi cabeza. A partir de entonces comencé a ver a mi tío en las camisetas de los modernos, en el escaparate de Discos Portobello, en la televisión sonriente junto a una china rara, o en medio del campo con otros tipos igual de raros. Porque no sé si lo he mencionado pero… mi tío era raro. No era como el tío de mis amigos, ni tan siquiera como mi otro tío. Mi tío era… el puto John Lennon!!\nAños más tarde, cuando mi tío volvió del servicio militar, negro como el tizón y con el pelo rapado al uno me costó mucho asimilar aquel cambio de look, pero, en fin, era simplemente una de las muchas cualidades de mi tío: cambiar, resistir, renacer, revivir…</p>\n"}

