	module.exports = {"title":"A Gaiteira: o patio do meu avó","date":"2015-12-01","layout":"post","path":"/gaiteira/","category":"memorias","description":"Na Gaiteira, ata o ano 2001, existiu un recuncho entre edificios de máis de sete pisos, xunto ao transformador de Fenosa, onde hoxe hai un aparcadoiro dunha empresa fantástica que fai todo moi ben e está dirixida por xente moi fermosa. Naquel recuncho, sen pavimentar, que os veciños e as veciñas arranxaron como se arranxan as casas en Tijuana, ou en Nairobi: con restos de todo, madeiros, chapas, cartaces abandonados, etc., vivían catro familias e había un negocio. O negocio facía que a visibilidade do recuncho, “o patio”, como lle chamábamos moitas, fora moito maior da que o seu atractivo natural proporcionaba. Alí estaba o zapateiro. Luís. Luís o zapateiro. O pai de Luisito e de Javi","body":"<p>Na Gaiteira, ata o ano 2001, existiu un recuncho entre edificios de máis de sete pisos, xunto ao transformador de Fenosa, onde hoxe hai un aparcadoiro dunha empresa fantástica que fai todo moi ben e está dirixida por xente moi fermosa. Naquel recuncho, sen pavimentar, que os veciños e as veciñas arranxaron como se arranxan as casas en Tijuana, ou en Nairobi: con restos de todo, madeiros, chapas, cartaces abandonados, etc., vivían catro familias e había un negocio. O negocio facía que a visibilidade do recuncho, “o patio”, como lle chamábamos moitas, fora moito maior da que o seu atractivo natural proporcionaba. Alí estaba o zapateiro. Luís. Luís o zapateiro. O pai de Luisito e de Javi.</p>\n<p>Pois naquel recuncho, no “patio”, que eu nunca souben chamar doutro xeito que non fose “o patio do meu abuelo”, vivía como unha familia máis a miña. O meu avó José, a miña avoa María e durante moitos anos tamén os meus dous tíos e a miña nai. No Nadal xuntabámonos 10 e máis a xantar nun comedor/cociña/habitación/salita de non máis de 8 ou 9 metros cadrados.</p>\n<p>Hoxe daquilo xa non queda nada. Nin as persoas. Algunhas (as máis) mortas e outras moi lonxe. Hoxe daquel “patio” só quedan as lembranzas dun neno que aprendeu a patinar, a xogar a buxaina, a escalar, a xogar ao fútbol, a voleibol, a baloncesto, a arranxar a bici, a facer sticks de hockey con paus, a… botar de menos. Sobre todo a botar de menos. E a odiar: ás empresas fermosas que constrúen casas enriba da xente, ás institucións miopes que miran para outro lado, e a todas aquelas persoas que non teñen memoria. Si, liches ben: “o patio do meu abuelo” durou ata o ano 2001, xusto o ano que morreu o meu avó, en concreto a miña avoa, pois eran todo un, que hasta xuntos morreron.</p>\n<p>Esa Coruña xa non está e, sen lembranzas, nin sequera as nosas fillas saberán dela. É a nosa obriga recordar.</p>\n<p>O outro día outro avó, tamén da Gaiteira, atopou esta foto nunha publicación da Coruña, “r-evolución Coruña” disque se chama, e polo visto houbo unha exposición no Kiosko Alfonso alá polo 2010. A foto é dun tal Xosé Castro (Pepucho). Gracias Pepucho. Así era o meu avó, co seu mandil, tan macho como o que máis, tendendo a roupa (recoñezo algunha miña e do meu irmán na foto). Ata que morreu ninguén foi quen de meter unha máquina no “patio”.</p>\n<p><img src=\"gaiteira.jpg\" alt=\"josé o da gaiteira\"></p>\n"}

