	module.exports = {"title":"Ponte las pilas con el software libre","date":"2016-03-04","layout":"post","path":"/ponte-las-pilas-con-el-software-libre/","category":"desarrollo web","description":"El software libre ha permanecido durante mucho tiempo alejado del usuario final. Alejado en el sentido técnico, puesto que las interfaces gráficas  de muchas soluciones de software libre estaban diseñadas exclusivamente para un usuario avanzado con un conocimiento técnico muy alto, cuando no exclusivamente para los desarrolladores.","body":"<p>El software libre ha permanecido durante mucho tiempo alejado del usuario final. Alejado en el sentido técnico, puesto que las interfaces gráficas  de muchas soluciones de software libre estaban diseñadas exclusivamente para un usuario avanzado con un conocimiento técnico muy alto, cuando no exclusivamente para los desarrolladores.</p>\n<p>Cada uno de los agentes (desarrollador, diseñador, usuario final) está muchas veces ciego hacia las necesidades del resto. En el caso del usuario final, inexperto y sin formación técnica, esa ceguera es comprensible, no tanto así en el caso del desarrollador y mucho menos en el ausente diseñador.</p>\n<p>Por suerte algunos proyectos de software libre como <a href=\"http://wordpress.org\">Wordpress</a> han roto con esa dinámica oscurantista pero son muchos más los proyectos interesantes y fáciles de usar que existen en el universo del software libre: <a href=\"https://www.odoo.com\">Odoo</a>, <a href=\"https://moodle.org\">Moodle</a>…</p>\n<p>En <a href=\"https://fricolab.com\">FricolaB</a> siempre hemos querido contribuir a la democratización del software libre y para ello es fundamental diseñar software sencillo, flexible e interoperable. Nos apasionan proyectos como <a href=\"https://www.docker.com\">Docker</a> o <a href=\"https://sandstorm.io\">Sandstorm</a> que contribuyen a hacer el despliegue y la instalación de software un juego de niños.</p>\n<p>Hoy presentamos una herramienta que pretende ser nuestra humilde contribución a esta tendencia de democratización del software libre, nuestro <strong>Catálogo OSS</strong>. Se trata de una recopilación de proyectos de software en las principales categorías: gestión de proyectos, comercio electrónico, diseño de sitios web estáticos, soluciones de e-learning, gestión de clientes…</p>\n<p>Nuestro <a href=\"http://fricolab.github.io/es/oss/\">Catálogo OSS</a> te ayudará a\nidentificar cual es el software\nlibre que más se acomoda a las necesidades de tu proyecto. Además de filtrar el software por categoría también podrás ordenarlos por popularidad, de manera que aquellos proyectos con más seguidores y colaboradores en <a href=\"https://github.com\">Github</a> aparecerán en primer lugar.</p>\n<p>Ponte las pilas con el software libre y libérate de las limitaciones del software privativo, así como de sus licencias restrictivas. Un proyecto original e innovador precisa de herramientas a medida. En <a href=\"https://fricolab.com\">FricolaB</a> te ayudamos a personalizar cualquiera de las aplicaciones de nuestro catálogo para conseguir los mejores resultados.</p>\n"}

