	module.exports = {"title":"Lo complejo, lo emergente, lo diferente que viene","date":"2016-03-11","layout":"post","path":"/lo-complejo-lo-emergente-lo-diferente-que-viene/","category":"pensamiento","description":"Desde Foucault a Badiou, pasando por Rancière o Deleuze, han contribuido a la construcción de ese pensamiento que se articula en torno a una unidad indescifrable, una metafísica imposible, un no-lugar al que siempre acababan regresando mis ensayos académicos en la universidad. Mi trabajo de investigación abordaba la posibilidad de una ética congruente con este pensamiento, que trascendiera la clásica dicotomía entre éticas de principios y éticas consecuencialistas.","body":"<style>\n      * {\n        font-family: Georgia, Cambria, \"Times New Roman\", Times, serif;\n      }\n      html, body {\n        margin: 0;\n        padding: 0;\n      }\n      h1 {\n        font-size: 50px;\n        margin-bottom: 17px;\n        color: #333;\n      }\n      h2 {\n        font-size: 24px;\n        line-height: 1.6;\n        margin: 30px 0 0 0;\n        margin-bottom: 18px;\n        margin-top: 33px;\n        color: #333;\n      }\n      h3 {\n        font-size: 30px;\n        margin: 10px 0 20px 0;\n        color: #333;\n      }\n      header {\n        width: 640px;\n        margin: auto;\n      }\n      section {\n        width: 640px;\n        margin: auto;\n      }\n      section p {\n        margin-bottom: 27px;\n        font-size: 20px;\n        line-height: 1.6;\n        color: #333;\n      }\n      section img {\n        max-width: 640px;\n      }\n      footer {\n        padding: 0 20px;\n        margin: 50px 0;\n        text-align: center;\n        font-size: 12px;\n      }\n      .aspectRatioPlaceholder {\n        max-width: auto !important;\n        max-height: auto !important;\n      }\n      .aspectRatioPlaceholder-fill {\n        padding-bottom: 0 !important;\n      }\n      header,\n      section[data-field=subtitle] {\n        display: none;\n      }\n      </style></head><body><article class=\"h-entry\">\n<section data-field=\"body\" class=\"e-content\">\n<section name=\"9b6c\" class=\"section section--body section--first section--last\"><div class=\"section-content\"><div class=\"section-inner sectionLayout--insetColumn\"><figure name=\"20a9\" id=\"20a9\" class=\"graf graf--figure graf--layoutOutsetLeft graf-after--h3\"><div class=\"aspectRatioPlaceholder is-locked\" style=\"max-width: 500px; max-height: 375px;\"><div class=\"aspectRatioPlaceholder-fill\" style=\"padding-bottom: 75%;\"></div><img class=\"graf-image\" data-image-id=\"0*tI3wc-Lwm9mpyUAy.jpg\" data-width=\"500\" data-height=\"375\" src=\"https://d262ilb51hltx0.cloudfront.net/max/600/0*tI3wc-Lwm9mpyUAy.jpg\"></div><figcaption class=\"imageCaption\">Michel Foucault Stencil</figcaption></figure><p name=\"7b56\" id=\"7b56\" class=\"graf graf--p graf-after--figure\">Hace tiempo la construcción del pensamiento de la diferencia se rumiaba casi con exclusividad en los pasillos de Paris 8, <a href=\"http://philosophie.univ-paris8.fr/\" data-href=\"http://philosophie.univ-paris8.fr/\" class=\"markup--anchor markup--p-anchor\" rel=\"nofollow\" target=\"_blank\">http://philosophie.univ-paris8.fr/</a>. Desde Foucault a Badiou, pasando por Rancière o Deleuze, todos ellos han contribuido a la construcción de ese pensamiento que se articula en torno a <strong class=\"markup--strong markup--p-strong\">una unidad indescifrable</strong>, <strong class=\"markup--strong markup--p-strong\">una metafísica imposible</strong>, un <strong class=\"markup--strong markup--p-strong\">no-lugar</strong> al que siempre acababan regresando mis ensayos académicos en la universidad. Mi trabajo de investigación abordaba la posibilidad de una ética congruente con este pensamiento, que trascendiera la clásica dicotomía entre éticas de principios y éticas consecuencialistas.</p><p name=\"f1dd\" id=\"f1dd\" class=\"graf graf--p graf-after--p\">En la actualidad no es ya Saint Denis la principal fuente a la que acudir para descubrir claves que puedan arrojar algo de luz sobre el problema de la <strong class=\"markup--strong markup--p-strong\">ética de lo diferente</strong>, pero es <strong class=\"markup--strong markup--p-strong\">de nuevo un francés</strong> el que me ha despertado la ilusión y la esperanza de articular <em class=\"markup--em markup--p-em\">un discurso ético moderno, a la altura de los tiempos</em> (que diría cualquier moderno que se precie).</p><figure name=\"0b9d\" id=\"0b9d\" class=\"graf graf--figure graf--startsWithDoubleQuote graf--layoutOutsetLeft graf-after--p\"><div class=\"aspectRatioPlaceholder is-locked\" style=\"max-width: 525px; max-height: 276px;\"><div class=\"aspectRatioPlaceholder-fill\" style=\"padding-bottom: 52.5%;\"></div><img class=\"graf-image\" data-image-id=\"0*lkiWW5hJSrxjk0fP.jpg\" data-width=\"800\" data-height=\"420\" src=\"https://d262ilb51hltx0.cloudfront.net/max/600/0*lkiWW5hJSrxjk0fP.jpg\"></div><figcaption class=\"imageCaption\">“Con post-it y rotuladores de colores cambiaremos el mundo”, Uno que se vino arriba</figcaption></figure><p name=\"f729\" id=\"f729\" class=\"graf graf--p graf-after--figure\">La obra de <strong class=\"markup--strong markup--p-strong\">Edgar Morin</strong> ha sido la disculpa para ponerme a investigar de nuevo <strong class=\"markup--strong markup--p-strong\">lo diferente</strong>, en términos de complejidad esta vez, y con una íntima relación con las <strong class=\"markup--strong markup--p-strong\">metodologías de diseño de proyectos</strong> que explican y dan forma a la experiencia del usuario. En su breve libro <a href=\"http://www.casadellibro.com/libro-introduccion-al-pensamiento-complejo/9789688521175/991683\" data-href=\"http://www.casadellibro.com/libro-introduccion-al-pensamiento-complejo/9789688521175/991683\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Introducción al pensamiento complejo</a> incorpora una reflexión sobre la complejidad en el mundo de la empresa que me ha permitido hacer una lectura de los principios del pensamiento de diseño (originalmente expuestos por <strong class=\"markup--strong markup--p-strong\">Tim Brown</strong>, fundador de <strong class=\"markup--strong markup--p-strong\">IDEO</strong> en su obra <a href=\"http://www.amazon.com/Change-Design-Transforms-Organizations-Innovation/dp/0061766089/ideobooks-20\" data-href=\"http://www.amazon.com/Change-Design-Transforms-Organizations-Innovation/dp/0061766089/ideobooks-20\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Change by Design</a>) mucho más rica e interesante que el discurso que comúnmente se presenta en las escuelas de negocios y en los artículos y charlas de los expertos en <strong class=\"markup--strong markup--p-strong\">design thinking</strong>.</p><blockquote name=\"99ea\" id=\"99ea\" class=\"graf graf--blockquote graf--startsWithDoubleQuote graf-after--p\">“Podemos decir, groseramente, que cuanto más compleja es una organización más tolera el desorden. Esto le da vitalidad, porque los individuos son aptos para tomar una iniciativa para arreglar tal o cual problema sin tener que pasar por la jerarquía central. Es un modo más inteligente de responder a los desafíos del mundo exterior. Pero un exceso de complejidad es, finalmente, desestructurante. En el límite, una organización que no tuviera más que libertades, y muy poco orden, se desintegraría, a menos que hubiera como complemento de esa libertad, una solidaridad profunda entre sus miembros.” Edgar MORIN, Introducción al pensamiento complejo, Gedisa, 2011</blockquote><p name=\"1e7c\" id=\"1e7c\" class=\"graf graf--p graf-after--blockquote\">He encontrado por el camino otros autores que asoman al discurso de la complejidad en las organizaciones, destacando <strong class=\"markup--strong markup--p-strong\">Dave Snowden</strong>, que enriqueció los planteamientos que durante los años 70 y 80 se cocinaron en instituciones como <a href=\"https://es.wikipedia.org/wiki/Xerox_PARC\" data-href=\"https://es.wikipedia.org/wiki/Xerox_PARC\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Xerox PARC</a>. Snowden ha desarrollado el <a href=\"https://en.wikipedia.org/wiki/Cynefin_Framework\" data-href=\"https://en.wikipedia.org/wiki/Cynefin_Framework\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Cynefin Framework</a>, aplicando la teoría de la complejidad (<a href=\"https://en.wikipedia.org/wiki/Complex_adaptive_system\" data-href=\"https://en.wikipedia.org/wiki/Complex_adaptive_system\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">sistemas adaptativos complejos</a>) al análisis de la <strong class=\"markup--strong markup--p-strong\">gestión del conocimiento en las organizaciones</strong>, en la que sabiamente identifica distintos dominios y distintas formas de aproximarse al conocimiento, desde el ámbito de lo <strong class=\"markup--strong markup--p-strong\">obvio</strong>, en el que se aplican la excelencia y el conocimiento previo, hasta el ámbito de la <strong class=\"markup--strong markup--p-strong\">complejidad</strong>, en el que no hay una maestría previa al experimento, y que entronca directamente con otras corrientes de gestión como el moviendo “lean” o el desarrollo ágil.</p><figure name=\"5f94\" id=\"5f94\" class=\"graf graf--figure graf-after--p\"><div class=\"aspectRatioPlaceholder is-locked\" style=\"max-width: 426px; max-height: 443px;\"><div class=\"aspectRatioPlaceholder-fill\" style=\"padding-bottom: 104%;\"></div><img class=\"graf-image\" data-image-id=\"0*jyhmKv75jOSjzjBR.png\" data-width=\"426\" data-height=\"443\" src=\"https://d262ilb51hltx0.cloudfront.net/max/800/0*jyhmKv75jOSjzjBR.png\"></div><figcaption class=\"imageCaption\">Cynefin Framework, by Dave Snowden</figcaption></figure><p name=\"6446\" id=\"6446\" class=\"graf graf--p graf-after--figure\">Se ha ido de esta manera enriqueciendo el planteamiento inicial de mis investigaciones en la universidad convirtiendo en complejo lo diferente, tamizado por una buena dosis de teoría de sistemas, lógica modal, biología evolutiva, mecánica cuántica y otras disciplinas que desde ángulos diversos abordan lo que para mí comienza a dibujarse como <strong class=\"markup--strong markup--p-strong\">El Problema</strong>. Otro hito importante en esta investigación lo marca el análisis fantástico que hace el divulgador <strong class=\"markup--strong markup--p-strong\">Phillip Ball</strong> en su obra más conocida, <a href=\"http://www.amazon.com/Critical-Mass-Thing-Leads-Another/dp/0374530416\" data-href=\"http://www.amazon.com/Critical-Mass-Thing-Leads-Another/dp/0374530416\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Critical Mass</a>. En ella presenta el repetido fenómeno que él caracteriza como <strong class=\"markup--strong markup--p-strong\">la ley de las potencias.</strong> A grosso modo esta ley describe la concentración espontánea de la mayoría de los elementos de una distribución en un número muy reducido de individuos, produciendo una distribución desigual y espontánea. Se trata de un comportamiento de los sistemas complejos adaptativos, que encontramos tanto en la distribución espacial de un organismo en su crecimiento fractal como en la distribución de servidores en la desorganizada red de redes o en el funcionamiento del aséptico y justo mercado, guiado por tan sabia “mano invisible”.</p><blockquote name=\"2205\" id=\"2205\" class=\"graf graf--blockquote graf--startsWithDoubleQuote graf-after--p\">“The probability distribution of incoming hyperlinks to HTML documents on the World Wide Web follows a power law, generating a straight line on this logarithmic plot. The outgoing links have a similar distribution. This implies that the Web is a scale-free network.(…)Power law seem to be a leitmotif in the Web (…)</blockquote><blockquote name=\"6f20\" id=\"6f20\" class=\"graf graf--blockquote graf-after--blockquote\">The idea of a self-organized critical economy is an appealing one. But sadly, like the sandpile model, it seems to be right in spirit but wrong in detail. (…) Power-law probability distributions, which do not discriminate (as Gaussian fluctuations do) against extreme events, may turn out to be a pervasive feature of the way people organize their affairs.” Philip BALL, Critical Mass: How One Thing Leads to Another, 2005</blockquote><figure name=\"55f5\" id=\"55f5\" class=\"graf graf--figure graf--layoutOutsetLeft graf-after--blockquote\"><div class=\"aspectRatioPlaceholder is-locked\" style=\"max-width: 525px; max-height: 273px;\"><div class=\"aspectRatioPlaceholder-fill\" style=\"padding-bottom: 52%;\"></div><img class=\"graf-image\" data-image-id=\"0*Sf5rAmqM7KMPxEEd.png\" data-width=\"1000\" data-height=\"520\" src=\"https://d262ilb51hltx0.cloudfront.net/max/600/0*Sf5rAmqM7KMPxEEd.png\"></div><figcaption class=\"imageCaption\">Power law function - criticalidad autoorganizada, o la ley del 99%</figcaption></figure><p name=\"196f\" id=\"196f\" class=\"graf graf--p graf-after--figure\">Patrones, atractores: el límite del caos. Ahora bien, avanzar desde el reconocimiento de <strong class=\"markup--strong markup--p-strong\">atractores evolutivos</strong> como los que plantean desde el Instituto de Santa Fe, entre otros, <a href=\"http://www.amazon.com/Reinventing-Sacred-Science-Reason-Religion/dp/0465018882/ref=sr_1_4?s=books&amp;ie=UTF8&amp;qid=1457696527&amp;sr=1-4\" data-href=\"http://www.amazon.com/Reinventing-Sacred-Science-Reason-Religion/dp/0465018882/ref=sr_1_4?s=books&amp;ie=UTF8&amp;qid=1457696527&amp;sr=1-4\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Stuart Kauffman</a>, y que matizan la teoría de la evolución tal y como los neodarwinistas la conciben, o la existencia de una <strong class=\"markup--strong markup--p-strong\">criticalidad autoorganizada,</strong> hasta el reconocimiento de una <strong class=\"markup--strong markup--p-strong\">ética universal, </strong>derivada de estos planteamientos científicos y congruente con los planteamientos de los filósofos de la diferencia, no es algo inmediato ni evidente, más allá de que la intuición inicial se vaya fortaleciendo y vaya adquiriendo una consistencia teórica que antes ni sospechaba.</p><p name=\"d210\" id=\"d210\" class=\"graf graf--p graf-after--p\">Surgen además unos nuevos actores en el debate, <strong class=\"markup--strong markup--p-strong\">los chicos malos de la metafísica</strong>, que hartos de la capacidad infinita del <strong class=\"markup--strong markup--p-strong\">correlacionismo</strong> -criticado por <a href=\"https://es.wikipedia.org/wiki/Quentin_Meillassoux\" data-href=\"https://es.wikipedia.org/wiki/Quentin_Meillassoux\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Quentin Meillassoux</a> en <em class=\"markup--em markup--p-em\">Après la finitude-</em> para poner al hombre y su medida en el medio de cualquier debate sobre el conocimiento, inauguran un nuevo espacio de saber con la inquietante etiqueta de “<em class=\"markup--em markup--p-em\">realismo especulativo</em>”. Solamente definir la naturaleza de este espacio exigiría un monográfico de cientos de hojas, y otros lo han hecho ya antes, como aquí en la entrada de <a href=\"https://en.wikipedia.org/wiki/Speculative_realism\" data-href=\"https://en.wikipedia.org/wiki/Speculative_realism\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Wikipedia</a>. Dentro de este grupo encontramos además autores de una originalidad extrema, tanto en sus planteamientos teóricos como en su afiliación disciplinaria, como Ian Bogost y su <a href=\"http://bogost.com/books/alien-phenomenology/\" data-href=\"http://bogost.com/books/alien-phenomenology/\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Alien Phenomenologie</a>.</p><blockquote name=\"b17c\" id=\"b17c\" class=\"graf graf--blockquote graf--startsWithDoubleQuote graf-after--p\">“The unit reveals a feature of being that the thing and the object occlude. The density and the condensation of tiny ontology has a flip side: something is always something else, too: a gear in another mechanism, a relation in another assembly, a part in another whole. Whithin the black hole-like density of being, things undergo an expansion. The ontological equivalent of the Big Bang rests within every object. Being expands.” Ian BOGOST, Alien Phenomenologie, 2012</blockquote><figure name=\"e938\" id=\"e938\" class=\"graf graf--figure graf--layoutOutsetLeft graf-after--blockquote\"><div class=\"aspectRatioPlaceholder is-locked\" style=\"max-width: 396px; max-height: 612px;\"><div class=\"aspectRatioPlaceholder-fill\" style=\"padding-bottom: 154.5%;\"></div><img class=\"graf-image\" data-image-id=\"0*kNkKQ9-aCN9UMmxN.png\" data-width=\"396\" data-height=\"612\" src=\"https://d262ilb51hltx0.cloudfront.net/max/600/0*kNkKQ9-aCN9UMmxN.png\"></div><figcaption class=\"imageCaption\">Alien Phenomenology Book Cover</figcaption></figure><p name=\"b617\" id=\"b617\" class=\"graf graf--p graf-after--figure\">Un diseñador de videojuegos que hace aportaciones interesantes al debate sobre la <a href=\"https://en.wikipedia.org/wiki/Object-oriented_ontology\" data-href=\"https://en.wikipedia.org/wiki/Object-oriented_ontology\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">ontología orientada a objetos</a> (originalmente planteada por <strong class=\"markup--strong markup--p-strong\">Graham Harman</strong>, uno de los principales representantes del realismo especulativo) adjudicándole <strong class=\"markup--strong markup--p-strong\">esencia</strong> al software, al igual que antes Levi R. Bryant lo había hecho con cualquier símbolo en su fantástica obra <a href=\"http://www.amazon.com/The-Democracy-Objects-Levi-Bryant/dp/1607852047\" data-href=\"http://www.amazon.com/The-Democracy-Objects-Levi-Bryant/dp/1607852047\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\"><strong class=\"markup--strong markup--p-strong\">The Democracy of Objects</strong></a>.</p><p name=\"2795\" id=\"2795\" class=\"graf graf--p graf-after--p\">He tejido un puente entre el pensamiento complejo de un francés multiinstrumentista como <strong class=\"markup--strong markup--p-strong\">Edgar Morin</strong> con la emergente y siempre inquietante <strong class=\"markup--strong markup--p-strong\">inteligencia artificial</strong>, auspiciada a disciplina top por una sociedad que en el 67 los situacionistas caracterizaban como <a href=\"https://es.wikipedia.org/wiki/La_sociedad_del_espect%C3%A1culo\" data-href=\"https://es.wikipedia.org/wiki/La_sociedad_del_espect%C3%A1culo\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">La Sociedad del Espectáculo,</a> y que sin duda hoy podríamos caracterizar como <strong class=\"markup--strong markup--p-strong\">La Sociedad del Software</strong> -interesante en este sentido la obra de Lev Manovich, <a href=\"http://manovich.net/index.php/projects/software-takes-command\" data-href=\"http://manovich.net/index.php/projects/software-takes-command\" class=\"markup--anchor markup--p-anchor\" target=\"_blank\">Software Takes Command</a>.</p><blockquote name=\"dc2f\" id=\"dc2f\" class=\"graf graf--blockquote graf--startsWithDoubleQuote graf-after--p\">“Why should humanists, social scientists, media scholars, and cultural critics care about software? Because outside of certain cultural areas such as crafts and fine arts [esta afirmación habría que matizarla hoy, 2016, con el advenimiento de la cultura “maker”], software has replaced a diverse array of physical, mechanical, and electronic technologies used before the twenty-first century to create, store, distribute and access cultural artifacts.” Lev MANOVICH, Software Takes Command, 2013</blockquote><p name=\"f44a\" id=\"f44a\" class=\"graf graf--p graf-after--blockquote\">Sin embargo, aún vertiendo todas las referencias en un contenedor amable no se termina de perfilar la posibilidad de una ética universal por ninguna parte. Pues bien, la intuición apunta a la necesidad de conjugar y articular una ética que resuelva por una parte los dos discursos tradicionalmente enfrentados de la <strong class=\"markup--strong markup--p-strong\">ética de principios</strong>, kantiana, deontológica y la <strong class=\"markup--strong markup--p-strong\">ética mecanicista, funcional y teleológica</strong> que ha triunfado hasta erigirse gracias al discurso hegemónico del capitalismo como la única y verdadera. Pero esa conjugación, o superación, <strong class=\"markup--strong markup--p-strong\">no podrá ser dialéctica</strong>, y por lo tanto reducirse en una única ética alternativa que venga a suplantar a las otras dos, débiles e inmaduras, enfrentadas en la historia, <strong class=\"markup--strong markup--p-strong\">sino más bien de naturaleza cuántica</strong>, o compleja.</p><p name=\"37aa\" id=\"37aa\" class=\"graf graf--p graf-after--p\"><strong class=\"markup--strong markup--p-strong\">La ética de principios</strong>, desposeída de una filosofía moderna que sitúe al hombre en el centro del discurso y acuse al objeto de no tener propiedades que le son propias más allá de las que el sujeto le dona, tendrá que dar paso a una <strong class=\"markup--strong markup--p-strong\">ética orientada a objetos</strong>, que contemple en un mismo plano los símbolos, los animales, y el propio planeta, con las propiedades de lo complejo, que incluyen la característica de contener el todo en cada una de las partes, tal y como apreciábamos en la cita anterior de Bogost. <strong class=\"markup--strong markup--p-strong\">Una ética que democratice los derechos</strong> y los formule de manera independiente a un discurso que es blanco, occidental y judeo-cristiano, que libere al ser humano de la tarea de erigirse como legislador universal, y al resto de seres de esa ontología alienígena de la necesidad de someterse a la voluntad del hombre blanco.</p><p name=\"1b80\" id=\"1b80\" class=\"graf graf--p graf-after--p\"><strong class=\"markup--strong markup--p-strong\">Las éticas consecuencialistas</strong>, que viven su mayor esplendor con la universalización de la deuda como sistema de valor, deberán asumir la falacia de construir una sociedad justa basada en la maximización de las funciones de utilidad o felicidad cuyos algoritmos custodian unos pocos para nuestra mayor seguridad. Una sociedad basada en la deuda, como bien analiza el propio <strong class=\"markup--strong markup--p-strong\">Nietzsche</strong> en <em class=\"markup--em markup--p-em\">La genealogía de la moral</em> y recupera con mucho acierto <strong class=\"markup--strong markup--p-strong\">Maurizio Lazzarato</strong> en su reciente obra <em class=\"markup--em markup--p-em\">Gouverner par la dette</em> que no concibe la posibilidad de separar el trabajo y la supervivencia y que obliga a miles de millones a vivir en unas condiciones de precariedad absolutas.</p><p name=\"7185\" id=\"7185\" class=\"graf graf--p graf-after--p\">Y <strong class=\"markup--strong markup--p-strong\">ambas</strong> deberán ser sometidas a una crítica que las visualice como posibles estados de evolución generados por las condiciones de partida, la naturaleza de las relaciones entre los agentes, y la voluntad del 2% que atesora más del 50% de los derechos, del valor, e incluso de la identidad, posibitando la generación de modelos alternativos.</p><p name=\"0254\" id=\"0254\" class=\"graf graf--p graf-after--p\"><strong class=\"markup--strong markup--p-strong\">Ontología, conocimiento y ética caminan juntas</strong>. La física y la biología nos han abierto los ojos, mediante un nuevo conocer, a una nueva realidad que está condenada a ser independiente del hombre, o a desaparecer bajo su voluntad. La mayor crítica que el realismo especulativo hace al llamado correlacionismo es su <strong class=\"markup--strong markup--p-strong\">incapacidad de comprender fenómenos materiales como el calentamiento global o la extinción de las especies</strong>, más allá de una lectura de la realidad ingenua del capitalismo, bien sea en su vertiente más conservadora, todo es como debe ser, o en su variedad hipster-posmoderna: el fin de la historia, la demolición de los grandes relatos para el establecimiento de un único gran relato, el de <strong class=\"markup--strong markup--p-strong\">la deuda</strong>.</p><p name=\"06c2\" id=\"06c2\" class=\"graf graf--p graf-after--p graf--last\">Así pues, y con la celeridad exigida por un mundo que avanza al ritmo vertiginoso de Internet, tenemos la obligación de concebir una ética que nos sirva por un lado para <strong class=\"markup--strong markup--p-strong\">salir del impasse histórico ética de principios vs ética teleológica</strong>, pero que además nos preserve, como especie y como planeta, del <strong class=\"markup--strong markup--p-strong\">advenimiento de una nueva era</strong> en la que la inteligencia artificial, que no será nunca inteligente, sino <strong class=\"markup--strong markup--p-strong\">superinteligente</strong> (consúltese la obra de Nick Bostrom al respecto), no dudará en someter al resto de agentes a su voluntad. Quizá, experimentada en nuestras propias carnes, lleguemos a comprender la injusticia y la insostenibilidad de una gobernanza global al servicio de una única especie.</p></div></div></section>\n</section>\n</article>\n</body>\n"}

