	module.exports = {"title":"Actualizando EduCaas con Rancher 1.2","date":"2016-12-14","layout":"post","path":"/actualizando-educaas-rancher-nueva-version/","category":"desarrollo web","description":"Desde hace aproximadamente un año estoy metido hasta las orejas en EduCaaS, un proyecto que busca proporcionar a instituciones y equipos que aprenden una infraestructura digital ágil y autónoma. EduCaaS está basado en Rancher, un software de código abierto para montar tu propio servicio de contenedores en la nube. Rancher acaba de actualizarse a la versión 1.2 y la actualización es un proceso sencillo pero con algunos pasos clave.","body":"<p>Desde hace aproximadamente un año estoy metido “hasta las orejas” en <a href=\"http://educaas.io\"><strong>EduCaaS</strong></a>, un proyecto que busca proporcionar a instituciones y equipos que aprenden una infraestructura digital ágil y autónoma. <strong>EduCaaS</strong> está basado en <a href=\"https://rancher.com\"><strong>Rancher</strong></a>, un software de código abierto para montar tu propio servicio de contenedores en la nube. Rancher acaba de actualizarse a la versión 1.2 y la actualización es un proceso sencillo pero con algunos pasos clave.</p>\n<p>EduCaaS utiliza el backend de Rancher “as-it-is”, sin ningun hackeo ni ninguna modificación en la API, pero sin embargo en la interfaz de usuario hemos hecho algunos <strong>cambios sustanciales</strong> que hacen que la actualización o la incorporación de las nuevas funcionalidades en <a href=\"https://github.com/educaas/ui\"><strong>nuestro fork</strong></a>** no sea automática y haya que resolver algunos conflictos a mano.</p>\n<h2>Actualización del backend</h2>\n<p>En EduCaaS estamos utilizando un nuevo servicio de contenedores de <strong>Rackspace</strong> que se llama <a href=\"https://ui.getcarina.com/\"><strong>Carina</strong></a> y que nos está dando muy buenos resultados en cuanto a la estabilidad y la capacidad. El coste, <strong>gratis mientras que está en BETA</strong>, es otra de las ventajas, obviamente. Utilizamos Carina para alojar el nodo principal del cluster de servidores que componen EduCaaS. Ese nodo contiene el servidor de Rancher y la base de datos general, que maneja la información de los usuarios y la configuración del entorno: autenticación de los usuarios, catálogos disponibles, etc.</p>\n<p>Existen varias configuraciones posibles. Nosotros hemos optado por alojar la base de datos en un contenedor independiente, siguiendo los pasos indicados en la documentación oficial de Rancher: <a href=\"https://docs.rancher.com/rancher/v1.2/en/installing-rancher/installing-server/#single-container-external-database\">https://docs.rancher.com/rancher/v1.2/en/installing-rancher/installing-server/#single-container-external-database</a></p>\n<p>Aprovisionamos el usuario y la base de datos directamente al lanzar el contenedor con un comando similar a este:</p>\n<pre><code>docker <span class=\"hljs-builtin-name\">run</span> --name educaas_db -e <span class=\"hljs-attribute\">MYSQL_NAME</span>=database_name <span class=\"hljs-attribute\">MYSQL_ROOT_PASSWORD</span>=my-secret-pw -d mariadb:tag\n</code></pre>\n<p>De esta manera podremos enlazar el contenedor que ejecuta el servidor de Rancher a esta base de datos externa.</p>\n<p>Pues bien, con esta configuración actualizar el servidor de Rancher es tan sencillo como parar el contenedor actual (y borrarlo) y lanzar un nuevo contenedor con la misma configuración tras descargar la última versión de Rancher con el comando:</p>\n<pre><code>docker pull rancher/<span class=\"hljs-keyword\">server</span>:latest\n</code></pre>\n<h2>Actualización del frontend</h2>\n<p>Aquí la actualización no es tan sencilla. En la versión original de Rancher la interfaz de usuario está integrada en el servidor pero en nuestro caso hemos instalado una versión personalizada. La personalización es un proceso que se describe con detalle en el archivo <a href=\"http://README.md\">README.md</a> original del proyecto: <a href=\"https://github.com/EduCaaS/UI#hosting-remotely\">https://github.com/EduCaaS/UI#hosting-remotely</a></p>\n<p>Primero actualizaremos la UI para integrar los cambios de la última versión de Rancher y luego exportaremos los archivos estáticos a nuestro servidor en la misma localización que la versión anterior para que el servidor siga apuntando a la interfaz correcta. Esto sería:</p>\n<ol>\n<li>Creamos una rama en nuestro repo con la última versión de Rancher: <em>git branch -b rancher-master</em></li>\n<li>Integramos en esa rama la última versión de nuestra interfaz: <em>git pull <a href=\"https://github.com/educaas/ui\">https://github.com/educaas/ui</a> master</em></li>\n<li>Resolvemos los conflictos editando manualmente los archivos que Git no es capaz de integrar de manera automática</li>\n<li>Subimos los cambios a nuestro repositorio como una rama nueva: <em>git push origin new-branch</em></li>\n<li>Una vez que tenemos las dos ramas en el repositorio y hemos comprobado que la nueva versión funciona hacemos un merge e integramos los cambios en la rama master y borramos la nueva rama.</li>\n</ol>\n<p>De esta manera tenemos ya nuesta plataforma actualizada a la última versión de Rancher, que incorpora <a href=\"https://github.com/rancher/rancher/releases/tag/v1.2.0\"><strong>muchas nuevas funcionalidades</strong></a> que todavía no hemos explorado con detalle pero que seguro que permitirá mejorar la experiencia de nuestros usuarios, sobre todo por la posibilidad de integrar el servicio de <strong>registro único</strong> también con el nodo principal de EduCaaS y la funcionalidad de <strong>gestión de volúmenes mejorada</strong>, que contribuirá a proporcionar más seguridad y fiabilidad al entorno.</p>\n"}

