	module.exports = {"title":"Guarda el contenido de la web con Wallabag","date":"2016-12-26","layout":"post","path":"/guarda-contenido-web-wallabag/","category":"desarrollo web","description":"¿Alguna vez has visto como las pestañas de tu explorador crecían y crecían indefinidamente esperando poder leer con detalle ese artículo o esa nuevo proyecto que has descubierto por la noche con el móvil fuera de casa? No eres el único. Sin embargo una avería en el móvil o borrar la caché por error te puede dejar sin toda esa información. A mí me ha pasado. Sniff. Así que escapando de soluciones alojadas que no respeten mi privacidad decidí instalar Wallabag en mi propio servidor y así sincronizar el contenido desde mi PC y desde el móvil.","body":"<p>¿Alguna vez has visto como las pestañas de tu explorador crecían y crecían indefinidamente esperando poder leer con detalle ese artículo o esa nuevo proyecto que has descubierto por la noche con el móvil fuera de casa?</p>\n<p>¿Estás cansado de herramientas de sincronización de pestañas que no puncionan en el móvil?</p>\n<p>¿Usas Google Chrome, Safari o Firefox indiferentemente y quieres compartir el contenido de las páginas web que vas encontrando cuando navegas?</p>\n<p>No eres el único. Sin embargo una avería en el móvil o borrar la caché por error te puede dejar sin toda esa información. A mí me ha pasado. Sniff. Así que escapando de soluciones alojadas que no respeten mi privacidad (<strong>Pocket, iSync, Firefox Sync, Google Sync</strong>) decidí instalar <a href=\"https://www.wallabag.org/\"><strong>Wallabag</strong></a> en mi propio servidor y así sincronizar el contenido desde mi PC y desde el móvil.</p>\n<iframe src=\"https://player.vimeo.com/video/167435064\" width=\"640\" height=\"360\" frameborder=\"0\" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>\n<p><a href=\"https://www.wallabag.org/\"><strong>Wallabag</strong></a> es una alternativa de código abierto a <strong>Pocket</strong>, que es ahora mismo la solución independiente (que no pertenezca a Google, Apple o Microsoft), más utilizada.</p>\n<h2>Cómo lo hago</h2>\n<p>Para poner en marcha tu propio servicio para guardar contenido web y poder leerlo cómodamente más tarde desde cualquier dispositivo necesitas:</p>\n<ul>\n<li>\n<p>Un servidor en el que poder ejecutar un contenedor <strong>Docker</strong>. Existen muchos servicios que te permiten hacerlo de manera gratuita, nuestro preferido es <a href=\"https://getcarina.com/\"><strong>Carina</strong></a> que mientras que está en BETA nos permite usar sus servicios sin coste. Nosotros para Wallabag usamos un servidor virtual en <a href=\"https://www.digitalocean.com/\"><strong>Digital Ocean</strong></a> que por su sencillez y coste nos parece el más adecuado para la mayor parte de los proyectos.</p>\n</li>\n<li>\n<p>Una instalación de <strong>Wallabag</strong>, que, en el caso de que hagamos el despliegue con Docker y utilicemos la opción más sencilla que utiliza como backend SQLite y se ejecuta con el siguiente comando:</p>\n</li>\n</ul>\n<pre><code>docker <span class=\"hljs-keyword\">run</span><span class=\"bash\"> -p 80:80 wallabag/wallabag\n</span></code></pre>\n<ul>\n<li>\n<p>Cambiar el usuario/contraseña que viene por defecto (wallabag/wallabag) por uno privado</p>\n</li>\n<li>\n<p>Crear un cliente en la API para poder conectarlo a las extensiones de los navegadores: las instrucciones <strong><a href=\"http://doc.wallabag.org/en/master/developer/api.html?highlight=api#creating-a-new-api-client\">http://doc.wallabag.org/en/master/developer/api.html?highlight=api#creating-a-new-api-client</a></strong></p>\n</li>\n<li>\n<p>Instalar la extensión en tu navegador o la aplicación móvil en tu teléfono: <strong><a href=\"https://www.wallabag.org/pages/download-wallabag.html\">https://www.wallabag.org/pages/download-wallabag.html</a></strong></p>\n</li>\n</ul>\n<p>Ya dispones de un servicio propio para guardar ese contenido que quieres revisar al salir del evento, o esa referencia que has apuntado en una reunión y quieres leer con detalle al llegar a casa.</p>\n<h2>Otras características</h2>\n<p>Wallabag permite importar el contenido que ya tenías guardado en otros servicios de terceros, como: <strong>Pocket, Readability, Instapaper, Firefox Sync, Google Sync</strong> y además una posibilidad que nos ha parecido muy útil para integrar el flujo de trabajo, la posibilidad de generar un feed para leer desde tu cliente de noticias favorito, que yo he añadido a mi cuenta de <a href=\"https://feedly.com\"><strong>Feedly</strong></a>, el lector de RSS que utilizo desde que cerró <strong>Google Reader</strong>.</p>\n<p>Todo ventajas. En tu servidor y de código abierto. ¡Adiós a las pestañas infinitas en el navegador!</p>\n"}

