	module.exports = {"title":"OpenedX en contenedores Docker","date":"2016-12-30","layout":"post","path":"/openedx-contenedores-docker/","category":"desarrollo web","description":"Open edX es la plataforma de código abierto que alimenta los cursos de edX, el mayor proveedor de MOOCs formado por un consorcio de universidades de todo el planeta.  El código fuente de edX está de este modo disponible para toda la comunidad gracias al compromiso del consorcio con el software libre. Cualquier institución puede alojar su propia instancia de Open edX y ofrecer sus propios cursos. Pero la instalación y el despliegue de Open edX dista mucho de ser pan comido. Gracias a Docker y el trabajo de algunos de los miembros más activos de la comunidad hemos podido ofrecer Open edX de manera ágil y sencilla. Ha sido un viaje duro...","body":"<p><a href=\"https://open.edx.org\">Open edX</a> es la plataforma de código abierto que alimenta los cursos de <a href=\"https://www.edx.org/\">edX</a>, el mayor proveedor de MOOCs formado por un consorcio de universidades de todo el planeta.  El código fuente de edX está de este modo disponible para toda la comunidad gracias al compromiso del consorcio con el software libre. Cualquier institución puede alojar su propia instancia de Open edX y ofrecer sus propios cursos. Pero la instalación y el despliegue de Open edX dista mucho de ser pan comido. Gracias a Docker y el trabajo de algunos de los miembros más activos de la comunidad hemos podido ofrecer Open edX de manera ágil y sencilla. Ha sido un viaje duro…</p>\n<h2>El código</h2>\n<p>Sí, vale, de <strong>código abierto</strong>, bien. Pero, ¿cómo leches accedo al código de un proyecto que me dicen que es de código abierto? Bueno, pues existen muchas herramientas para compartir el código de un proyecto de software, pero la más común ahora mismo es <a href=\"htttps://github.com\">Github</a>. Github se ha convertido en los últimos cinco años en el principal fuente a la hora de alojar y colaborar en el software de código abierto, aunque, paradójicamente, el código de Github no está disponible. En <a href=\"https://about.gitlab.com/2016/07/20/gitlab-is-open-core-github-is-closed-source/\">este post</a> el CEO de <a href=\"https://about.gitlab.com/\">Gitlab</a>, principal alternativa libre a Github, nos amplia este “pequeño detalle”.</p>\n<p>En el caso de Open edX disponemos de un gran número de repositorios disponibles en su perfil de Github: <a href=\"https://github.com/edx\">https://github.com/edx</a>. El perfil es de los más activos que he visto, con modificaciones cada hora e incluso cada minuto, lo cual nos indica que el compromiso de Open edX con la comuniad de sofware abierto es alto y que su equipo es muy activo.</p>\n<p>El respositorio que contiene el código de la plataforma es <a href=\"https://github.com/edx/edx-platform\">https://github.com/edx/edx-platform</a> pero el repositorio que nos interesa para instalar o personalizar nuestra instalación es <a href=\"https://github.com/edx/configuration\">https://github.com/edx/configuration</a>. En él podremos encontrar recursos para proceder a la instalación de la plataforma, tanto en su versión de <strong>desarrollo</strong>, como la versión de <strong>producción</strong></p>\n<h2>Desarrollo, producción, nativa, vagrant,…</h2>\n<p>En la documentación del proyecto, que está un poco “desperdigada” entre <a href=\"https://openedx.atlassian.net/wiki/display/OpenOPS/Open+edX+Operations+Home\">Atlassian Confluence</a>, la <a href=\"https://github.com/edx/configuration/wiki\">Wiki de Github</a> y una <a href=\"http://edx.readthedocs.io/projects/edx-installing-configuring-and-running/en/latest/\">web propia de documentación</a>, podemos encontrar varias propuestas para proceder a la instalación de la plataforma Open edX: <a href=\"https://openedx.atlassian.net/wiki/display/OpenOPS/Open+edX+Installation+Options\">https://openedx.atlassian.net/wiki/display/OpenOPS/Open+edX+Installation+Options</a></p>\n<p>Básicamente hay dos modalidades de instalación:</p>\n<ul>\n<li><strong>Devstack</strong>: Interesante si vas a hacer modificaciones en el código de Open edX y más ligera, con menos servicios</li>\n<li><strong>Fullstack</strong>: Instalación que integra todos los servicios necesarios para tener Open edX en producción: lms, cms, forum, analytics, etc.</li>\n</ul>\n<p>También hay otras opciones de instalación disponibles en la Wiki, incluyendo una instalación nativa en Ubuntu 12.04, así como una máquina virtual de Bitnami. Desde hace unos días también hay instrucciones para hacer la instalación en Ubuntu 16.04: <a href=\"https://openedx.atlassian.net/wiki/display/OpenOPS/Native+Open+edX+Ubuntu+16.04+64+bit+Installation\">https://openedx.atlassian.net/wiki/display/OpenOPS/Native+Open+edX+Ubuntu+16.04+64+bit+Installation</a>. Los requisitos para la instalación son una máquina con al menos 4GB de RAM, una CPU de 2,00GHz y un mínimo de 25GB de RAM. Con estos requisitos nos podemos ir olvidando de instalar Open edX en un servidor compartido o en un droplet baratito de <a href=\"https://www.digitalocean.com/\">Digital Ocean</a>. Estos requisitos implican alquilar un servidor cuyo <strong>coste será de 50€/mes</strong>, aproximadamente.</p>\n<p>La opción más sencilla para disponer de una instalación de Open edX operativa es utilizar <a href=\"https://www.vagrantup.com/\">Vagrant</a> para aprovisionar una máquina virtual que contenga todos los servicios necesarios. La instalación <strong>fullstack</strong> con Vagarant está descrita aquí: <a href=\"https://openedx.atlassian.net/wiki/display/OpenOPS/Running+Fullstack\">https://openedx.atlassian.net/wiki/display/OpenOPS/Running+Fullstack</a>. Estas instrucciones indican además algunos usuarios de ejemplo para poder comenzar a utilizar Open edX. Este método puede ser útil para probar en nuestro servidor local o para una instalación sin muchas pretensiones, pero tener en producción todos los servicios en una máquina virtual no es lo más deseable. La razón fundamental es que si nos falla esa máquina… se caerá todo el sistema!! La otra razaón fundamental es que nos encontraremos con una infraestructura muy grande y su manejo no será muy ágil, en relación a escalado, copias de seguridad, etc.</p>\n<h2>Y en esto llegó Docker</h2>\n<p>Los que seguís mi actividad en las redes sociales sabéis que desde que me encontré con Docker, hace ya tres años, no dejo de hacer apología de su uso, pues considero que sus ventajas son infinitas frente a los métodos convencionales de ejecutar, construir y aprovisionar software. Existen sin duda motivos para no ser demasiado optimistas y razones para no “entregarnos totalmente a Docker”, como la dependencia de un único fabricante -recordemos que Docker no es un standar, sino un proyecto open source desarrollado por una compañía privada- o la existencia de otros proyectos similares que podrían tener las mismas prestaciones si contaran con los recursos suficientes, como <a href=\"https://github.com/opencontainers/runc\">https://github.com/opencontainers/runc</a> o <a href=\"https://github.com/lxc/lxc\">https://github.com/lxc/lxc</a>.</p>\n<p>Sin embargo, más allá de estas cuestiones de “gobernanza” que, no siendo poco relevantes, no afectan a la utilidad de Docker como herramienta, Docker es la principal apuesta de la comunidad open source para ejecutar y aprovisionar software en la nube. Una prueba de ello es la proliferación de servicios CAAS, las siglas de “Container as a Service”. Tanto Amazon con su <a href=\"https://aws.amazon.com/es/ecs/\">EC2 Container Service</a> como Rackspace con <a href=\"https://getcarina.com/\">Carina</a>, un proyecto que utilizamos en <a href=\"http://educaas.io\">EduCaaS</a> en su fase beta, o Microsoft y su recientemente anunciado <a href=\"https://azure.microsoft.com/es-es/services/container-service/\">servicio de contenedores</a> cuentan con plataformas en la nube basadas en Docker o en alguna de sus alternativas comerciales, como <a href=\"http://kubernetes.io/\">Kubernetes</a> (de Google) o <a href=\"http://mesos.apache.org/\">Mesos</a> (de Apache).</p>\n<h2>La dockerización de Open edX</h2>\n<p>La comunidad de desarrolladores alrededor de Open edX lleva ya años avanzando en el diseño de un método de instalación basado en Docker. Existe un canal de comunicación en el <a href=\"https://openedx-slack-invite.herokuapp.com/\">Slack oficial del proyecto</a> dedicado en exclusividad a este menester, pero no es todo lo activo que muchos desearíamos. Los avances en el repositorio oficial de Open edX no son muy grandes. Existen algunas indicaciones en la wiki del proyecto en relación a los avances ya realizados y las necesidades: <a href=\"https://openedx.atlassian.net/wiki/display/OpenOPS/Open+edX+on+Docker\">https://openedx.atlassian.net/wiki/display/OpenOPS/Open+edX+on+Docker</a></p>\n<p>En el repositorio oficial ya se incorporan desde hace algunos meses métodos para aprovisionar los distintos servicios usando Docker: <a href=\"https://github.com/edx/configuration/tree/master/docker/build\">https://github.com/edx/configuration/tree/master/docker/build</a>. Además, gracias a la colaboración del equipo de <a href=\"http://www.appsembler.com/\">Appsembler</a>, el principal proveedor a nivel internacional de servicios tecnológicos relacionados con Open edX, contamos con algunas imágenes “out-of-the-box” que nos permiten ejecutar Open edX en un único contenedor. Esta es la imagen que utilizamos desde hace ya tiempo en la instalación de pruebas que tenemos dispobile en <a href=\"http://fricolab.com\">FricolaB</a> <a href=\"http://mooc.fricolab.com\">aquí</a>. Me gustaría desde aquí aprovechar para agradecer personalmente a su CEO, Nate Aune, por su compromiso con el proyecto y su apoyo.</p>\n<p>Sin embargo, es desde el otro lado del mundo desde el que me ha llegado la inspiración y el talento para poder construir una instalación de Open edX basada en Docker y lo suficientemente estable y potente para alimentar una plataforma en producción. Descubrir este proyecto de <a href=\"https://github.com/borisno2\">Josh Calder</a> (fundador de <a href=\"http://www.opensaas.com.au/\">http://www.opensaas.com.au/</a>) ha sido fundamental: <a href=\"https://github.com/OpenSaasAU/edx-docker-ansible\">https://github.com/OpenSaasAU/edx-docker-ansible</a>. En él, Josh utiliza una nueva tecnología <strong>ansible-container</strong> que integra la capacidad de <a href=\"https://www.ansible.com/\">Ansible</a> para diseñar los aprovisionamientos con la potencia de <strong>Docker</strong>. De esta manera podemos reutilizar el repositorio oficial de Open edX y sus <strong>playbooks</strong> para construir una image independiente de cada uno de los servicios de los que está compuesto Open edX.</p>\n<h2>¿Open edX en EduCaaS?</h2>\n<p>Pero Josh ha ido más lejos aún y ha diseñado también una plantilla para integrar esa instalación <strong>dockerizada</strong> de Open edX en <a href=\"http://rancher.com/\">Rancher</a>, el backend que utiliza EduCaaS para orquestar y organizar el catálogo de aplicaciones disponible para nuestros clientes. En este proyecto, <a href=\"https://github.com/OpenSaasAU/RancherTemplates\">https://github.com/OpenSaasAU/RancherTemplates</a>, podemos obtener las plantillas para <strong>Rancher</strong>, que en unos días integraremos en EduCaaS para poder incluir Open edX en nuestro <a href=\"https://github.com/EduCaaS/catalog\">catálogo de aplicaciones</a>.</p>\n"}

