	module.exports = {"title":"Wordpress con LDAP en Docker","date":"2017-01-09","layout":"post","path":"/wordpress-ldap-docker/","category":"desarrollo web","description":"Si alguna vez has intentado activar algún plugin para la autenticación de tus usuarios en Wordpress con LDAP y tu instalación de Wordpress corre con Docker te habrás encontrado con un error de PHP que te impide ejecutar consultas LDAP. Hemos modificado la imagen oficial de Wordpress para que incluya de manera nativa soporte para LDAP. Aquí te lo cuento:","body":"<p><a href=\"https://wordpress.org\">Wordpress</a> no necesita presentación. Actualmente más del 25% de todos los sitios web funcionan con Wordpress. Su comunidad, su flexibilidad y su infinidad de plugins la han convertido en la plataforma web preferida para los que se inician en el desarrollo web y también para muchos otros proyectos de gran envergadura.</p>\n<p>En <a href=\"http://educaas.io\">EduCaaS</a> usamos Wordpress como pasarela de entrada para nuestro catálogo de aplicaciones y estamos integrando el catálogo de EduCaaS en Wordpress para que nuestros usuarios puedan hacer los despliegues desde un entorno familiar que puedan personalizar y adaptar a sus necesidades.</p>\n<p>Una de las características que nos ha hecho optar por <strong>Wordpress</strong> es la cantidad de plugins disponibles para ampliar sus funcionalidades. Poder utilizar un directorio de usuarios común para todas las aplicaciones web es una funcionalidad básica para nosotros, así que utilizamos un plugin de <strong>Wordpress</strong> que nos permita integrar sus usuarios con el directorio LDAP que nos proporciona nuestra solución de gestión de identidades digitales, <a href=\"http://wso2.com/products/identity-server/\">WSO2is</a>, para la que hemos creado una solución dockerizada que puedes encontrar en nuestro catálogo de apps, <a href=\"https://github.com/EduCaaS/catalog\">https://github.com/EduCaaS/catalog</a>.</p>\n<h2>LDAP en Wordpress</h2>\n<p>Pero nos hemos encontrado con que la versión oficial de <strong>Wordpress en Docker</strong> no soporta LDAP, por lo que hemos construido una plantilla nueva que está disponible en nuestro <a href=\"https://hub.docker.com/u/dalareo/\">registro público</a>, así como en el catálogo de <strong>EduCaaS</strong>.</p>\n<p>Los cambios que hemos hecho en la imagen original, que siguen las indicaciones de <a href=\"https://github.com/docker-library/wordpress/issues/37\">https://github.com/docker-library/wordpress/issues/37</a>, son los siguientes:</p>\n<pre><code>RUN \\\n    apt-get update &amp;&amp; \\\n    apt-get install libldap2-dev -y &amp;&amp; \\\n    rm -rf /var/<span class=\"hljs-class\"><span class=\"hljs-keyword\">lib</span>/<span class=\"hljs-title\">apt</span>/<span class=\"hljs-title\">lists</span>/* &amp;&amp; \\</span>\n    docker-php-ext-configure ldap --<span class=\"hljs-keyword\">with</span>-libdir=<span class=\"hljs-class\"><span class=\"hljs-keyword\">lib</span>/<span class=\"hljs-title\">x86_64</span>-<span class=\"hljs-title\">linux</span>-<span class=\"hljs-title\">gnu</span>/ &amp;&amp; \\</span>\n    docker-php-ext-install ldap\n</code></pre>\n<p>De esta manera ya puedes instalar cualquier plugin que le proporcione a tu Wordpress la posibilidad de funcionar como cliente de un directorio de usuarios en LDAP.</p>\n"}

