	module.exports = {"title":"Mostrar las etiquetas de Facebook correctamente con GatsbyJS","date":"2017-01-13","layout":"post","path":"/etiquets-facebook-gatsbyjs/","category":"desarrollo web","description":"Este blog está construido con GatsbyJS, un framework basado en ReactJS para producir sitios estáticos. Alojado en Github directamente su velocidad y eficiencia es brutal, pero ha habido que hacer algunos hackeos para que tuviera las mismas características que otro hecho con Jekyll u otro framework con una trayectoria mayor y una comunidad de usuarios produciendo plugins y complementos. Aquí explico como incluí las meta etiquetas de Facebook en cada post.","body":"<p>Al compartir el contenido del blog en las redes sociales éstas (Facebook sobre todo pero también Linkedin y otras) basan los “<em>snippets</em>” en las etiquetas que contiene tu contenido en la cabecera del documento HTML. <a href=\"https://github.com/gatsbyjs/gatsby\">GatsbyJs</a> por defecto usaba la librería <a href=\"https://github.com/gaearon/react-document-title\">https://github.com/gaearon/react-document-title</a> y yo la he sustituido por otra más potente, <a href=\"https://github.com/nfl/react-helmet\">https://github.com/nfl/react-helmet</a>.</p>\n<p>El proceso ha sido sencillo, y además está documentado en una <em>issue</em> del repositorio de <strong>GatsbyJS</strong>: <a href=\"https://github.com/gatsbyjs/gatsby/issues/488\">https://github.com/gatsbyjs/gatsby/issues/488</a></p>\n<p>Así se queda el código del <em>wrapper</em> que transforma el documento en formato <em>Markdown</em> que contiene el contenido del post en HTML:</p>\n<pre><code>        <span class=\"hljs-keyword\">return</span> (\n            &lt;div&gt;\n              &lt;Helmet\n                title={ `${post.title} - ${config.siteTitle}` }\n                meta={[\n                    {property: <span class=\"hljs-string\">\"og:title\"</span>, content: post.title },\n                    {property: <span class=\"hljs-string\">\"og:description\"</span>, content: post.description },\n                ]}\n              /&gt;\n              { template }\n            &lt;/div&gt;\n);\n</code></pre>\n"}

